.clearF:after {
  content: '';
  visibility: hidden;
  display: block;
  clear: both;
}

.showSocialistValues {
  position: absolute;
  color: #3f51b5;
  -webkit-animation: showSocialistValues 3.5s;
          animation: showSocialistValues 3.5s;
  opacity: 0
}

@-webkit-keyframes showSocialistValues {
  0% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0)
  }
  /*20%{opacity: 0.9;transform: translateY(10px)}*/
  /*40%{opacity: 0.7;transform: translateY(20px)}*/
  /*50%{opacity: 0.5;transform: translateY(30px)}*/
  /*80%{opacity: 0.2;transform: translateY(40px)}*/
  100% {
    opacity: 0;
    -webkit-transform: translateY(-50px);
            transform: translateY(-50px)
  }
}

@keyframes showSocialistValues {
  0% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0)
  }
  /*20%{opacity: 0.9;transform: translateY(10px)}*/
  /*40%{opacity: 0.7;transform: translateY(20px)}*/
  /*50%{opacity: 0.5;transform: translateY(30px)}*/
  /*80%{opacity: 0.2;transform: translateY(40px)}*/
  100% {
    opacity: 0;
    -webkit-transform: translateY(-50px);
            transform: translateY(-50px)
  }
}

._arrow_down {
  background: url(data:image/png;base64,) no-repeat center;
  background-size: 100%;
}

._arrow_down:hover {
  cursor: pointer;
}

.mouseOverShowMessage {
  position: absolute;
  z-index: 9999;
  left: 0;
  top: 0;
  display: none;
  background: rgba(0, 0, 0, 0.7);
  color: #fff;
  border: 1px solid #0f0f0f;
  border-radius: 4px;
  line-height: 16px;
  font-size: 13px;
  font-family: 微软雅黑;
  padding: 5px 6px;
}

@-webkit-keyframes opac {
  from {
    opacity: 1;
    width: 10px;
    height: 10px;
    top: calc(50% - 5px);
    left: calc(50% - 5px);
  }
  to {
    opacity: 0;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
  }
}

@keyframes opac {
  from {
    opacity: 1;
    width: 10px;
    height: 10px;
    top: calc(50% - 5px);
    left: calc(50% - 5px);
  }
  to {
    opacity: 0;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
  }
}
